<template>
  <div>
    <!--้ขๅๅฑๅฏผ่ชๅบๅ-->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }">้ฆ้กต</el-breadcrumb-item>
      <el-breadcrumb-item>OpenLayers</el-breadcrumb-item>
      <el-breadcrumb-item>ๅพๅฑๅค้ๆงไปถ</el-breadcrumb-item>
    </el-breadcrumb>
    <!--ๅก็ๅบๅ-->
    <el-card>
      <div id="map" style="width:100%;height:100%">
        <div id="mouse-position"></div>
        <div id="layerControl" class="layerControl">
          <div class="title"><label>ๅพๅฑๅ่กจ</label></div>
          <ul id="layerTree" class="layerTree"></ul>
        </div>
      </div>
    </el-card>
  </div>

</template>
<script>
import Map from 'ol/map';
import View from 'ol/view';
import TileLayer from 'ol/layer/tile';
import ImageLayer from 'ol/layer/image';
import TileGrid from 'ol/tilegrid/tilegrid';
import WMTSGrid from 'ol/tilegrid/wmts';
import OSM from "ol/source/osm";
import XYZ from 'ol/source/xyz';
import TileImage from 'ol/source/tileimage';
import ImageWMS from 'ol/source/imagewms';
import TileWMS from 'ol/source/tilewms';
import WMTS from 'ol/source/wmts';
import proj from 'ol/proj';
import extent from 'ol/extent';
export default {
  data() {
    return {
    }
  },
  created() {
  },
  mounted() {
    this.initMap()
  },
  methods: {
    initMap() {

      //ๅฎไพๅMapๅฏน่ฑกๅ่ฝฝๅฐๅพ
      let map = new Map({
        target: 'map', //ๅฐๅพๅฎนๅจdiv็ID
        //ๅฐๅพๅฎนๅจไธญๅ่ฝฝ็ๅพๅฑ
        layers: [this.osmMap(), this.baiduMap(), this.tianMap(), this.wmsImage(), this.wmsTile(), this.wmsGrid(), this.wmtsMap()],
        //ๅฐๅพ่งๅพ่ฎพ็ฝฎ
        view: new View({
          center: [0, 0], //ๅฐๅพๅๅงไธญๅฟ็น
          zoom: 2 //ๅฐๅพๅๅงๆพ็คบ็บงๅซ
        })
      });
      console.log(map);
      //ๅ่ฝฝๅพๅฑๅ่กจๆฐๆฎ
      this.loadLayersControl(map, "layerTree");
    },
    //OSMๅฐๅพ
    osmMap() {

      const openStreetMap = new TileLayer({
        source: new OSM(),
        visible: true,
        name: 'OSMๅฐๅพ'
      })
      return openStreetMap;
    },
    //็พๅบฆๅฐๅพ
    baiduMap() {

      let projection = proj.get("EPSG:3857");
      let resolutions = [];
      for (let i = 0; i < 19; i++) {
        resolutions[i] = Math.pow(2, 18 - i);
      }
      let tilegrid = new TileGrid({
        origin: [0, 0],
        resolutions: resolutions
      });

      let baidu_source = new TileImage({
        projection: projection,
        tileGrid: tilegrid,
        tileUrlFunction: function (tileCoord, pixelRatio, proj) {
          if (!tileCoord) {
            return '';
          }
          let z = tileCoord[0];
          let x = tileCoord[1];
          let y = -tileCoord[2] - 1;
          return "http://online1.map.bdimg.com/onlinelabel/?qt=tile&x=" + x + "&y=" + y + "&z=" + z + "&styles=pl&scaler=1&udt=20191119";
        },
      });

      const baiduMap = new TileLayer({
        source: baidu_source,
        visible: true,
        name: '็พๅบฆๅฐๅพ'
      })
      return baiduMap;
    },
    //ๅคฉๅฐๅพ
    tianMap() {

      const tianMap = new TileLayer({
        source: new XYZ({
          url: "http://t4.tianditu.gov.cn/DataServer?T=vec_w&x={x}&y={y}&l={z}&tk=49ea1deec0ffd88ef13a3f69987e9a63"
        }),
        visible: true,
        name: 'ๅคฉๅฐๅพ'
      })
      return tianMap;
    },
    //wmsImageๆๅก
    wmsImage() {

      let wmsImageSource = new ImageWMS({
        url: 'https://ahocelet.com/geoserver/wms', //WMSๆๅกๅบๅฐๅ
        params: { 'LAYERS': 'topp:states' }, //ๅพๅฑๅๆฐ
        ratio: 1,
        serverType: 'geoserver', //ๆๅก็ฑปๅ
      });
      return new ImageLayer({
        extent: [-13884991, 2870341, -7455066, 6338219], //ๆฐๆฎ่ๅด
        source: wmsImageSource,
        visible: true,
        name: 'wmsImageๅฐๅพ'
      });
    },
    //wmsTileๆๅก
    wmsTile() {

      let wmsTileSource = new TileWMS({
        url: 'https://ahocelet.com/geoserver/wms', //WMSๆๅกๅฐๅ
        params: { 'LAYERS': 'ne:ne_10m_admin_0_countries', 'TILED': true }, //ๅพๅฑ็ญๅๆฐ
        serverType: 'geoserver'//ๆๅก็ฑปๅ
      });
      return new TileLayer({
        source: wmsTileSource,
        visible: true,
        name: 'wmsTileๅฐๅพ'
      });
    },
    //wmsGridๆๅก
    wmsGrid() {
      //้่ฟ่ๅด่ฎก็ฎๅพๅฐๅ่พจ็ๆฐ็ปๅฏน่ฑกresolutions
      let projExtent = proj.get('EPSG:3857').getExtent();
      let startResolution = extent.getWidth(projExtent) / 256;
      let resolutions = new Array(22);
      for (let i = 0, ii = resolutions.length; i < ii; ++i) {
        resolutions[i] = startResolution / Math.pow(2, i);
      }
      //ๅฎไพๅol.tilegrid.TileGridๅฏน่ฑก
      let tileGrid = new TileGrid({
        extent: [-13884991, 2870341, -7455066, 6338219], //ๆฐๆฎ่ๅด
        resolutions: resolutions, //ๅ่พจ็ๆฐ็ป
        tileSize: [512, 256] //็ฆ็ๅคงๅฐ
      });
      //ไฝฟ็จTileLayerๅฎไพๅWMSๅพๅฑๅฏน่ฑก๏ผ่ฎพ็ฝฎol.source.TileWMS็tileGridๅๆฐ
      let wmsGridSource = new TileWMS({
        url: 'https://ahocelet.com/geoserver/wms', //WMSๆๅกๅฐๅ
        params: { 'LAYERS': 'topp:states', 'TILED': true }, //ๅพๅฑ็ญๅๆฐ
        serverType: 'geoserver', //ๆๅก็ฑปๅ
        tileGrid: tileGrid  //็ฆ็็ฝๆผๅฏน่ฑกๅๆฐ๏ผ็ฆ็ๅคงๅฐไธบ512x256๏ผ
      })

      return new TileLayer({
        source: wmsGridSource,
        visible: true,
        name: 'wmsGridๅฐๅพ'
      });
    },
    //WMTSๆๅก
    wmtsMap() {
      //้่ฟ่ๅด่ฎก็ฎๅพๅฐๅ่พจ็ๆฐ็ป
      let projection = proj.get('EPSG:3857');
      let projectionExtent = projection.getExtent();
      let size = extent.getWidth(projectionExtent) / 256;
      let resolutions = new Array(14);
      let matrixIds = new Array(14);
      for (let z = 0; z < 14; ++z) {
        // generate resolutions and matrixIds arrays for this WMTS
        resolutions[z] = size / Math.pow(2, z);
        matrixIds[z] = z;
      }
      let wmtsSource = new WMTS({
        attributions: // ๆฐๆฎๆบไฟกๆฏ
          'Tiles ยฉ <a href="https://services.arcgisonline.com/arcgis/rest/' +
          'services/Demographics/USA_Population_Density/MapServer/">ArcGIS</a>',
        url: //WMTSๆๅกๅบๅฐๅ
          'https://services.arcgisonline.com/arcgis/rest/' +
          'services/Demographics/USA_Population_Density/MapServer/WMTS/',
        layer: '0',
        matrixSet: 'EPSG:3857', //ๆๅฝฑๅๆ็ณป่ฎพ็ฝฎ็ฉ้ต
        format: 'image/png', //ๅพ็ๆผๅผ
        projection: projection, //ๆฐๆฎ็ๆๅฝฑๅๆ็ณป
        //็ฆ็็ฝๆผๅฏน่ฑก
        tileGrid: new WMTSGrid({
          origin: new extent.getTopLeft(projectionExtent), //ๅ็น๏ผๅทฆไธ่ง๏ผ
          resolutions: resolutions, //ๅ่พจ็ๆฐ็ป
          matrixIds: matrixIds, //็ฉ้ตๆ่ฏๅ่กจ๏ผไธๅฐๅพ็บงๆฐไฟๆไธ่ด
        }),
        style: 'default',
        wrapX: true,
      })
      //ๅฎไพๅWMTSๆๅกๅพๅฑๅฏน่ฑก๏ผTileLayer๏ผol.source.WMTS๏ผ
      return new TileLayer({
        opacity: 0.7, //ๅพๅฑ้ๆๅบฆ
        source: wmtsSource,
        visible: true,
        name: 'wmtsMapๅฐๅพ'
      });
    },

    /**
     * ๅ่ฝฝๅพๅฑๅ่กจๆฐๆฎ
     * @param {ol.Map} map ๅฐๅพๅฏน่ฑก
     * @param {string} id ๅพๅฑๅ่กจๅฎนๅจID
     */
    loadLayersControl(map, id) {
      let layer = new Array();  //mapไธญ็ๅพๅฑๆฐ็ป
      let layerName = new Array();  //ๅพๅฑๅ็งฐๆฐ็ป
      let layerVisibility = new Array();  //ๅพๅฑๅฏ่งๅฑๆงๆฐ็ป
      let treeContent = document.getElementById(id); //ๅพๅฑ็ฎๅฝๅฎนๅจ
      let layers = map.getLayers(); //่ทๅๅฐๅพไธญๆๆๅพๅฑ
      for (let i = 0; i < layers.getLength(); i++) {
        //่ทๅๆฏไธชๅพๅฑ็ๅ็งฐใๆฏๅฆๅฏ่งๅฑๆง
        layer[i] = layers.item(i);
        layerName[i] = layer[i].get('name');
        layerVisibility[i] = layer[i].getVisible();

        //ๆฐๅขliๅ็ด๏ผ็จๆฅๆฟ่ฝฝๅพๅฑ้กน
        let elementLi = document.createElement('li');
        treeContent.appendChild(elementLi); // ๆทปๅๅญ่็น
        //ๅๅปบๅค้ๆกๅ็ด
        let elementInput = document.createElement('input');
        elementInput.type = "checkbox";
        elementInput.name = "layers";
        elementLi.appendChild(elementInput);
        //ๅๅปบlabelๅ็ด
        let elementLable = document.createElement('label');
        elementLable.className = "layer";
        //่ฎพ็ฝฎๅพๅฑๅ็งฐ
        this.setInnerText(elementLable, layerName[i]);
        elementLi.appendChild(elementLable);

        //่ฎพ็ฝฎๅพๅฑ้ป่ฎคๆพ็คบ็ถๆ
        if (layerVisibility[i]) {
          elementInput.checked = true;
        }
        this.addChangeEvent(elementInput, layer[i]);  //ไธบcheckboxๆทปๅๅๆดไบไปถ
      }
    },
    /**
     * ไธบcheckboxๅ็ด็ปๅฎๅๆดไบไปถ
     * @param {input} element checkboxๅ็ด
     * @param {ol.layer.Layer} layer ๅพๅฑๅฏน่ฑก
     */
    addChangeEvent(element, layer) {
      element.onclick = function () {
        if (element.checked) {
          layer.setVisible(true); //ๆพ็คบๅพๅฑ
        } else {
          layer.setVisible(false); //ไธๆพ็คบๅพๅฑ
        }
      };
    },
    /**
     * ๅจๆ่ฎพ็ฝฎๅ็ดๆๆฌๅๅฎน๏ผๅผๅฎน๏ผ
     */
    setInnerText(element, text) {
      if (typeof element.textContent == "string") {
        element.textContent = text;
      } else {
        element.innerText = text;
      }
    }
  }
}
</script>

<style lang="less" scoped>
/* ๅพๅฑๆงไปถๅฑๆทๅผ่ฎพ็ฝฎ */
.layerControl {
  position: absolute;
  bottom: 5px;
  min-width: 200px;
  max-height: 250px;
  right: 40px;
  top: 130px;
  z-index: 2001;
  /*ๅจๅฐๅพๅฎนๅจไธญ็ๅฑ๏ผ่ฆ่ฎพ็ฝฎz-index็ๅผ่ฎฉๅถๆพ็คบๅจๅฐๅพไธๅฑ*/
  color: #ffffff;
  background-color: #4c4e5a;
  border-width: 10px;
  /*่พน็ผ็ๅฎฝๅบฆ*/
  border-radius: 10px;
  /*ๅ่ง็ๅคงๅฐ */
  border-color: #000 #000 #000 #000;
  /*่พนๆก้ข่ฒ*/
}

.layerControl .title {
  font-weight: bold;
  font-size: 15px;
  margin: 10px;
}

.layerTree li {
  list-style: none;
  margin: 5px 10px;
}

/* ้ผๆไฝ็ฝฎๆงไปถๅฑๆทๅผ่ฎพ็ฝฎ */
#mouse-position {
  float: left;
  position: absolute;
  bottom: 5px;
  width: 200px;
  height: 20px;
  z-index: 2000;
  /*ๅจๅฐๅพๅฎนๅจไธญ็ๅฑ๏ผ่ฆ่ฎพ็ฝฎz-index็ๅผ่ฎฉๅถๆพ็คบๅจๅฐๅพไธๅฑ*/
}
</style>
